@use "../base/settings";
@use "../base/base";


header {
  height: 5rem;
  background: white;
  box-shadow: 0 0.4rem 2rem 0 rgba(0, 0, 0, 0.06);
  display: flex;

  .basic-grid {
    display: flex;
    align-items: center;
    box-sizing: border-box;
  }
}

.header__logo {
  width: 4rem;
  height: fit-content;
}

.header__ticket-info {
  flex-grow: 1;
  text-align: center;
  color: settings.$blue;
}