@use '../../base/base';
@use '../../base/settings';

.details__title {
    color: settings.$blue
}

path.recharts-sector {
    stroke: none;
}

.details__list-container {
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.details__data-container {
    display: grid;
    grid-template-columns: 10rem 1fr;
    align-items: center;
    gap: 2.75rem;
}