//Typography

@mixin font-face($family, $src, $weight, $style) {
  @font-face {
    font-family: $family;
    src: url($src);
    font-weight: $weight;
    font-style: $style;

  }
}

//Heebo
@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@300;400;500;700;800&family=Miriam+Libre:wght@700&display=swap');


$font_main: 'Heebo', sans-serif;;
$font_secondary: 'Miriam Libre', sans-serif;;

//Colors
$light-blue: #1A8AFF;
$blue: #444FB2;
$medium-blue: #1A8AFF;
$grey: #726F6F;
$light-grey: #D9D9D9

