@use "../base/settings";
@use "../base/base";


.main-container {
  background-color: settings.$blue;
  height: 36.7rem;
  border-radius: 1.6rem;
  width: 100%;
  margin: auto 20.7rem;
  padding: 0 4.6rem;
  display: grid;
  grid-template-columns: 45% 55%;
  align-items: center;
  position: relative;
  z-index: 1;

  &.video {
    grid-template-columns: 100%;
    padding: 0;

    .main-container__content-box { 
      padding: 0;
    }
  }
}

.main-container__content {
  padding-left: 3.1rem;
  color: white;
  height: 60%; 
  
  .parag_16 {
    padding-left: 5rem;
  }

}

.main-container__content-box {
  background-color: white;
  border-radius: 1.3247rem;
  box-shadow: 0 0 3rem 0 rgba(0, 0, 0, 0.15);
  height: 120%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  overflow: hidden;
  padding: 5.7rem;
  box-sizing: border-box;
}

.bottom-strip {
  height: 4.7rem;
  border-top: 0.2rem solid settings.$blue;

  .basic-grid {
    position: relative;
  }
}

.bottom-strip__container {
  position: absolute;
  bottom: 0;
  right: 0;
  width:24rem;
  height: 20rem;
  z-index: 2;
  

  img {
    position: absolute;
    bottom: 0;
    left: 0;
  }

}
