@use "../base/settings";
@use "../base/base";
@use "../components/basic-button";

.login-form {
 display: grid;
 width: 75%;
 row-gap: 4.3rem;
 margin: auto;
}

.login-form__input {
    height: 4rem;
    border-radius: 10rem;
    border: 1px solid settings.$grey;
    padding: 0 2rem;
    font-family: settings.$font_main;

    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
      }
}