@use 'settings';

html {
  font-size: 10px;
}

body,
p {
  margin: 0;
  font-family: settings.$font_main;
  letter-spacing: -0.017rem;
}

@for $i from 1 through 5 {
  h#{$i} {
    margin: 0;
  }
}

button {
  border: none;
  background: none;
  font-family: settings.$font_main;
  padding: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

//Layout

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;

}

.basic-grid {
  width: 128rem;
  margin: auto;

  &.main-content {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
}

//Type
.head {

  &_40 {
    font-size: 4.5rem;
    line-height: 101.5%;
    font-weight: 700;
    margin-bottom: 3.5rem;

    .light {
      font-weight: 300;
    }
  }

  &_30 {
    font-family: settings.$font_secondary;
    font-size: 3rem;
    font-weight: 700;
    line-height: 140%;
    letter-spacing: -0.017rem;
  }

  &_20 {
    font-family: settings.$font_secondary;
    font-size: 2rem;
    line-height: 2.61rem;
    letter-spacing: 0.0104rem;
    letter-spacing: 0.0129rem;
  }

}

.parag {

  &_18 {
    font-size: 1.8rem;
    line-height: 140%;
    font-weight: 300;
  }

  &_16 {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 120%;
  }

}

//Visual
video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}