@use '../../base/base';
@use '../../base/settings';

.improve__points {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    

    li {
        display: grid;
        grid-template-columns: 5.6rem 1fr;
        align-items: center;
        gap: 2.75rem;
    }
}

.improve__image-container {
    width: 5.6rem;
    height: 6.3rem;
    flex: 0 0 5.6rem;

    img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
    }
}