@use '../base/settings.scss';
@use '../base/base.scss';

.slider {
    display: flex;
    flex-direction: column;
    width: 11.4rem;
    row-gap: 5.2rem;
    position: absolute;
    right: 0;
    z-index: 10;

    button {
        font-size: 1.6rem;
        color: settings.$blue;
        text-align: right;
        font-weight: 400;
    
        
        &.active {
            font-weight: 800;
            position: relative;

            &:after {
                content:'';
                width: 2.4rem;
                height: 0.2rem;
                position: absolute;
                background-color: settings.$blue;
                border-radius: 1rem;
                margin-top: 0.5rem;
                right:0;
                top: 2.5rem;
            }

        }
    }
}